.header {
    background-image: url('../../../images/pattern-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 35vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    position: relative;
    
    &__heading{
        color: white;
        font-weight: 500;
        margin: 0.5em 0;
    }

    &__input{
        width: 100%;
        padding: 1em;
        border-radius: 1em;
        
        
    }

    &__form{
        width: 60%;
        display: flex;
        flex-direction: row;
        position: relative;
        
        &-img{
            border-radius: 0 1em 1em 0;
            background-color: black;
            position: absolute;
            top: 0 ;
            right: 0;
            padding: calc(1em + 1.566px);
             
             
        }
    }
   
}